import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';
import { DataResolveService } from 'src/app/services/DataShare/data-resolve.service';

const routes: Routes = [
  {
    path: 'tabs',
    component: TabsPage,
    children: [
      {
        path: 'tab1',
        children: [
          {
            path: '',
            loadChildren: () =>import('../Profile/tab1.module').then(m => m.Tab1PageModule)
          }
        ]
      },
      {
        path: 'chat',
        children:[
            {
              path: '',
              loadChildren:() =>  import('../chat/chat.module').then(m => m.ChatPageModule)
            }
          ]
      },
      {
        path: 'tab2',
        children: [
          {
            path: '',
            loadChildren: () =>import('../Offers/tab2.module').then(m => m.Tab2PageModule)
          }
        ]
      },
      {
        path: 'tab3',
        children: [
          {
            path: '',
            loadChildren: () =>import('../Home/tab3.module').then(m => m.Tab3PageModule)
          }
        ]
      },
      {
        path: 'tab4',
        children: [
          {
            path: '',
            loadChildren: () =>import('../Request/tab4.module').then(m => m.Tab4PageModule)
          }
        ]
      },
      {
        path: 'getintouch',
        children: [
          {
            path: '',
            loadChildren: () =>import('../../pages/get-in-touch/get-in-touch.module').then(m => m.GetInTouchPageModule)
          }
        ]
      },
      {
        path: 'request-details/:id',
        resolve: {
          dataSharing: DataResolveService
        },
        loadChildren: './pages/request-details/request-details.module#RequestDetailsPageModule'
      },
      {
        path: 'service-provider',
        loadChildren: './pages/userService/serviceProviders/service-provider/service-provider.module#ServiceProviderPageModule'
      },
      {
        path: 'service-provider/:id',
        resolve: {
          dataSharing: DataResolveService
        },
        loadChildren: './pages/userService/serviceProviders/service-provider/service-provider.module#ServiceProviderPageModule'
      },
      {
        path: 'provided-services/:id',
        resolve: {
          dataSharing: DataResolveService
        },
        loadChildren: './pages/userService/serviceProviders/provided-services/provided-services.module#ProvidedServicesPageModule'
      },
      { 
        path: 'provided-details/:id', 
        resolve: {
          dataSharing: DataResolveService
        },
        loadChildren: './pages/userService/serviceProviders/provided-details/provided-details.module#ProvidedDetailsPageModule' 
      },
      { 
        path: 'medical-history/:id', 
        resolve: {
          dataSharing: DataResolveService
        },
        loadChildren: './pages/medical-history/medical-history.module#MedicalHistoryPageModule' },
        {
          path: 'onlin-shopping',
          loadChildren: './pages/onlin-shopping/onlin-shopping.module#OnlinShoppingPageModule' 
         },
         { 
           path: 'about-us', 
           loadChildren: './pages/about-us/about-us.module#AboutUsPageModule' },
    ]
  },
  {
    path: '',
    redirectTo: '/tabs/tab3',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TabsPageRoutingModule { }
